import {profileAPI} from "../api/api";
import {setTotalUsersCount, setUsers, toggleIsFetching} from "./users-reducer";
import {stopSubmit} from "redux-form";

const ADD_POST = 'ADD-POST';
const SET_USER_PROFILE = 'SET_USER_PROFILE';
const SET_STATUS = 'SET_STATUS';
const DELETE_POST = 'DELETE_POST';
const SAVE_PHOTO_SUCCESS = 'SAVE_PHOTO_SUCCESS';

let initialState = {  // 
    posts: [
        {id: 1, message: 'Hi!', likeCount: 23 },
        {id: 2, message: "You like this?", likeCount: 31 },
        {id: 3, message: "I'm fine!", likeCount: 10 },
        {id: 4, message: 'Are you?!', likeCount: 29 }
    ],
    profile: [],
    status: ""
}

const profileReducer = (state = initialState, action) => {
    switch (action.type){
        case ADD_POST : {
            let newPost = {
                id: 5, message: action.newPostText, likeCount: 0
            }
            return {
                ...state,
                posts: [ ...state.posts, newPost]
            };
        }
        case SET_USER_PROFILE: {
            return {...state, profile: action.profile}
        }
        case SET_STATUS: {
            return {...state, status: action.status}
        }
        case DELETE_POST: {
            return {...state, posts: state.posts.filter(u => u.id != action.postId)}
        }
        case SAVE_PHOTO_SUCCESS: {
            return  { ...state, profile: {...state.profile, photos: action.photos} }
        }
        default:
            return state
    }

}

export const addPostKey = (newPostText) => ({ type: ADD_POST, newPostText })
export const setUserProfile = (profile) => ({ type: SET_USER_PROFILE, profile })
export const setStatus = (status) => ({ type: SET_STATUS, status })
export const deletePost = (postId) => ({ type: DELETE_POST, postId })
export const savePhotoSuccess = (photos) => ({ type: SAVE_PHOTO_SUCCESS, photos })

export const getUserProfileHeader = (userId) => async (dispatch) => {
    let response = await profileAPI.getUserProfile(userId)
    dispatch(setUserProfile(response))
}

export const getStatus = (userId) => async (dispatch) => {
    let response = await profileAPI.getUsersStatus(userId)
    dispatch(setStatus(response))
}

export const updateStatus = (status) => async (dispatch) => {
    let response = await profileAPI.updateStatus(status)
    if (response.resultCode === 0) {
        dispatch(setStatus(status))
    }
}

export const savePhoto = (file) => async (dispatch) => {
    let response = await profileAPI.savePhoto(file)
    if (response.resultCode === 0) {
        dispatch(savePhotoSuccess(response.photos))
    }
}

export const saveProfile = (profile) => async (dispatch, getState) => {
    debugger
    const userId = getState().auth.userId
    let response = await profileAPI.saveProfile(profile)
    if (response.data.resultCode === 0) {
        dispatch(getUserProfileHeader(userId))
    } else {
        let message = response.data.messages.length > 0 ? response.data.messages[0] : 'Some error'
        dispatch(stopSubmit('profile', {_error: message}))
        return Promise.reject(response[0])
    }
}

export default profileReducer;